import React, { useEffect, useState } from 'react'
import {
    CRow,
    CCol,
    CCard,
    CCardHeader,
    CCardBody,
    CLabel,
    CInput,
    CInputGroup,
    CInputGroupAppend,
    CButton,
    CSelect,
} from '@coreui/react'

import { ColorPicker, useColor, toColor } from 'react-color-palette'
import "react-color-palette/lib/css/styles.css"

var Color = require('color');

const ColorConverter = () => {
    const [colorPalette, setColorPalette] = useColor("hex", "#121212");
    const [color, setColor] = useState(Color("#121212"))
    const [hex, setHEX] = useState("")

    const setColorHEX = (e) => {
        var data = e.target && e.target.value;
        setHEX(data);

        if (data && data.length === 7) {
            setColorPalette(toColor("hex", data));
        }
    }

    // useEffect(() => {
    //     if (!color) return;
    //     console.log(color.toJSON());
    // }, [color])

    useEffect(() => {
        try {
            var color = Color(colorPalette.hex)
            setColor(color)
        }
        catch (e) {
            alert(e.message)
        }
    }, [colorPalette])

    const copyToClipboard = (result) => {
        var tempElem = document.createElement('textarea');
        tempElem.value = result.toString()
        document.body.appendChild(tempElem);

        tempElem.select();
        document.execCommand("copy");
        document.body.removeChild(tempElem);

        alert(`{ ${tempElem.value} } is Copied`);
    }

    const ResultColorGroup = (data) => {
        var result = data.color;
        return (
            <CInputGroup className="mb-2">
                {
                    result &&
                        Array.isArray(result)
                        ?
                        result.map((color, i) => {
                            return (
                                <CInput key={i} value={color} disabled />
                            )
                        })
                        :
                        <CInput value={result} disabled />
                }
                <CInputGroupAppend>
                    <CButton type="button" color="dark" onClick={(e) => { copyToClipboard(result) }}>Copy</CButton>
                </CInputGroupAppend>
            </CInputGroup>
        )
    }

    return (
        <CRow>
            <CCol lg={12}>
                <CCard>
                    <CCardBody>
                        <CRow>
                            <CCol lg={8}>
                                <CCard key='color_converter' style={{ marginBottom: 0 }} >
                                    <CCardHeader>
                                        Color Converter
                                    </CCardHeader>
                                    <CCardBody>
                                        <CRow style={{ padding: '1rem 0 1rem 0', lineHeight: '35px' }}>
                                            <CCol md="2">
                                                <CSelect custom name="src_color" id="src_color" disabled>
                                                    <option value="HEX"> HEX </option>
                                                    <option value="RGB"> RGB </option>
                                                    <option value="HSV"> HSV </option>
                                                    <option value="CMYK"> CMYK </option>
                                                </CSelect>
                                            </CCol>
                                            <CCol xs="12" md="10">
                                                <CInputGroup>
                                                    <CInput id="hex" value={hex} onChange={setColorHEX} placeholder="Input Hex string '#xxxxxx'"></CInput>
                                                </CInputGroup>
                                                {/* <span className="input_hex">Input Hex string '#xxxxxx' </span> */}
                                            </CCol>
                                            {/* <CCol xs="12" md="2">
                                                <CButton style={{ width: '100%' }} color="dark"> Convert </CButton>
                                            </CCol> */}
                                        </CRow>
                                        {color && <>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HEX </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.hex()} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> RGB </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.rgb().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HSV </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.hsv().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HSl </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.hsl().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> HWB </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.hwb().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> CMYK </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.cmyk().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> LAB </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.lab().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> ANSI16 </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.ansi16().color} />
                                                </CCol>
                                            </CRow>
                                            <CRow style={{ lineHeight: '35px' }}>
                                                <CCol md="2" style={{ textAlign: 'center' }}>
                                                    <CLabel><b> ANSI256 </b></CLabel>
                                                </CCol>
                                                <CCol md="10">
                                                    <ResultColorGroup color={color.ansi256().color} />
                                                </CCol>
                                            </CRow>
                                        </>
                                        }
                                    </CCardBody>
                                </CCard>
                            </CCol>
                            <CCol lg={4} style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-around', alignItems: 'center' }}>
                                <ColorPicker width={400} height={256} color={colorPalette} onChange={setColorPalette} hideHSV hideRGB hideHEX dark />
                                <div style={{ width: '400px', height: '160px', backgroundColor: colorPalette.hex, borderRadius: '10px' }} />
                            </CCol>
                        </CRow>
                    </CCardBody>
                </CCard>
            </CCol>
        </CRow>
    )
}

export default ColorConverter
